html.lenis {
  height: auto;
}

.lenis.lenis-smooth {
  scroll-behavior: auto;
}

.lenis.lenis-smooth [data-lenis-prevent] {
  overscroll-behavior: contain;
}

.lenis.lenis-stopped {
  overflow: hidden;
}

.lenis.lenis-scrolling iframe {
  pointer-events: none;
}

body {
  font-feature-settings: "palt";
  font-family: "ryo-gothic-plusn", sans-serif;
  color: #254850;
  overscroll-behavior: none;
  transition: opacity 0.5s ease;
}
body ::selection {
  color: #ffffff;
  background-color: #254850;
}
body.loaded, body.visited {
  opacity: 1;
}
body .hidden {
  visibility: hidden;
}

.bold {
  font-weight: bold;
}

@media screen and (min-width: 750px) {
  .show {
    opacity: 0;
    transform: translate3d(0, 10rem, 0) scale(0.95);
    will-change: opacity, transform;
    transition: transform cubic-bezier(0.19, 1, 0.22, 1) 2s, opacity cubic-bezier(0.19, 1, 0.22, 1) 2s;
  }
  .show.active {
    opacity: 1;
    transform: translate3d(0, 0, 0) scale(1);
  }
  #loading {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
  }
  #loading .logo-wrap {
    margin: 0 auto;
    width: 50rem;
  }
  #loading .logo {
    position: relative;
  }
  #loading .logo.active svg {
    opacity: 1;
  }
  #loading .logo.fill .fill-target {
    opacity: 1;
  }
  #loading .logo .fill-target {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%) scale(1) rotate(0);
    opacity: 0;
    width: 100%;
    transition: opacity 0.5s ease;
  }
  #loading .logo svg {
    opacity: 0;
  }
  #loading .logo path {
    fill-opacity: 0; /*透過0で塗りの色が見えない状態*/
    transition: fill-opacity 0.5s; /*0.5秒で色が付くアニメーション*/
    fill: none; /*塗りの色*/
    stroke: #000; /*線の色*/
  }
  #loading .logo.done path {
    fill: #000; /*塗りの色*/
    fill-opacity: 1; /*透過1で塗りの色が見える状態*/
    stroke: none; /*線の色*/
  }
}